@import '../../sizes';
@import '../../colors';
@import '../../mixins';

.container {
  padding: 5px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;

  @media screen and (min-width: $mobileL) {
    padding: 10px 5px;
  }
  &Wrapper {
    width: 100%;
    max-width: $mobileL;
  }
}

.currentImg {
  -o-object-fit: contain;
     object-fit: contain;
  -o-object-position: center;
     object-position: center;
  height: 300px;
  width: 100%;

  &Container {
    background-color: $gray;
    width: 100%;
    height: 300px;
    margin-bottom: 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;

    @media screen and (min-width: $mobileL) {
      height: $mobileL;
    }
  }
}

.gallery {
  &Container {
    width: 100%;
    height: 125px;
    position: relative;
    overflow-x: hidden;
  }
  &Feed {
    height: 100%;
    position: absolute;
    overflow-x: hidden;
    overflow-y: hidden;
    top: 0;
    left: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }

  &Img {
    width: 125px !important
  }

  &Img {
    -o-object-fit: contain;
       object-fit: contain;
    -o-object-position: center;
       object-position: center;
    height: 125px;
    cursor: pointer;
    &Container {
      width: 125px !important;
    }
    &Container {
      cursor: pointer;
      background-color: $gray;
      height: 125px;
      margin-right: 5px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      pointer-events: fill;
    }
  }

  &BtnPrev,
  &BtnNext {
    border: none;
    outline: none;
    background: none;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.1);
    @include primaryText;
    color: $white;
    font-size: 20px;
    height: 100%;
    position: absolute;
    top: 0;
    width: 30px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    &:hover {
      background-color: rgba(0, 0, 0, 0.4);
    }
  }
  &BtnPrev {
    left: 0;
  }
  &BtnNext {
    right: 0;
  }
}
